*{
    margin: 0px;
    padding: 0px;
    font-family: "Noto Sans", sans-serif;

}
.con1{
    height: 96vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.first_heading{
    margin-left: 100px;
}
.name{
    color: aliceblue;
    margin-top: 30px;
    font-size: 60px;
   
}
.first_heading_quote{
    color: aliceblue;
}
.demoweb{
    overflow: hidden;
    height: 90vh;
    width: 80vw;
   border-top: 4px solid white;
   border-left: 1px solid white;
   border-right: 1px solid white;
 border-radius: 5px;
    margin: 50px;
    background-color: rgb(44, 44, 44);

}
.typer{
    font-size: 16px;
    color: yellow;
}
.DemoHeader{
    height: 30px;
    width: 100%;
    background-color: black;
    
}
.headerTab{
    background-color: rgb(44, 44, 44);
padding: 5.5px 10px;
display: inline-block;
    color: rgb(217, 176, 99);
    font-size: 14px;
   
}
.icon{
/* border: 2px solid red; */
/* margin-top: 5px; */
float: right;
}
.ic{
    color: white;
    margin: 2px;
    height: 20px;
    width: 15px;
    margin: 0px 8px;
}

.err{
padding: 10px;
}
.err p{
   
    color:wheat;
    font-size: 16px;
    margin-bottom: 10px;
}
.err li{
    color:wheat;
    font-size: 16px;
    list-style: none;
    margin-left: 20px;
}
b{
    color: rgb(26, 161, 210);
}
big{
    color: rgb(113, 203, 113);
}

.terminal{
    /* border: 2px solid red; */
    height: 100%;
    width: 100%;
   border-top: 1px solid rgb(83, 73, 73);
    background-color: rgb(26, 20, 11);
    margin-top: 30px;
    
}
.terminalheader{
    display: flex;
  
}
.terminalheader li{
color: rgb(143, 143, 143);
list-style: none;
margin: 5px 10px;
}



.expertiese{
    height: 70vh;
    width: 92vw;
    /* border: 2px solid red; */
    margin: auto
}
.expertiese p{
    font-family: "Noto Sans", sans-serif;
 color: rgb(131, 255, 131);
 font-size: 60px;
 text-align: center;
 margin-top: 40px;
}
.excontainer{
   
    height: 80%;
    width: 99%;
    /* border: 2px solid wheat; */
    display: flex;
    align-items: center;
    justify-content: center;
}
.skill{
    height: 80%;
    width: 25%;
    border: 2px solid white;
    padding: 10px;
    background-color: rgb(20, 20, 20);
}
.skill span{
   color: rgb(221, 239, 255);
   font-size: 20px;
   margin-left: 40px;
   margin-top: 30px;
   margin-bottom: 30px;
   display: block;
   border-left: 2px solid rgb(131, 255, 131);
   padding: 0px 5px;
}
.skill h4{
   color:  rgb(26, 161, 210);
   font-size: 16px;
   margin-top: 20px;
}
.skill h2{
   color: rgb(146, 146, 146);
   font-size: 50px;
   box-shadow: 2px 2px 2px rgb(131, 255, 131);
  
}

.project{
    
    /* border: 2px solid red; */
    width: 80vw;
    margin: auto;
    color: aliceblue;
}
.project p{
font-size: 60px;
color: rgb(26, 161, 210);
text-align: center;
margin-top: 30px;
}
.imagep{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
}
img{
    border-radius: 30px;
    height: 300px;
    width: 550px;
    /* box-shadow: 10px 10px 500px rgb(26, 161, 210) ; */
    margin: 50px;
    opacity: 0.9;
    transition: all 1s;
    &:hover{
        opacity: 1;
    }
   
}
@media (max-width: 700px) {
    .first_heading{
        margin-left: 30px;
    }
    .name{
        font-size: 30px;
    }
    .typer{
        font-size: 12px;
    }
    .err p{
        font-size: 11px;
    }
    .err li{
        font-size: 11px;
    }
   
    .terminalheader li{
      font-size: 12px;
        }
        .expertiese p{
            font-size: 30px;
           }
           .expertiese{
            height: 60vh;
        }
        .skill{
            width: 45%;
        }
        .skill span{
            font-size: 16px;
            margin-left: 20px;
            margin-top: 10px;
            margin-bottom: 10px;
            padding: 0px 5px;
         }
         .skill h2{
            font-size: 30px;
         }
         .project p{
            font-size: 30px;
            }
            img{
                height: 200px;
                width: 320px;
                margin: 0px;
                margin-bottom: 10px;
                margin-left: 10px;
            }
            .imagep{
               flex-direction: column;
            }
}
